import { Autocomplete, TextField, FormControl } from "@mui/material";
import { useEffect, useState } from "react";

const SymptomSelector = ({
  symptoms,
  symptomList,
  jsonSymptoms,
  updateFields,
  id,
  label,
  didSubmit
}) => {
  const [didEdit, setDidEdit] = useState(false);
  const [symptomValues, setSymptomValues] = useState([]);

  useEffect(() => {
    if (didSubmit) {
      setSymptomValues([]);
    }
  }, [didSubmit]);

  return (
    <FormControl sx={{ width: "90%" }}>
      <Autocomplete
        disablePortal
        autoHighlight
        size="small"
        id={id}
        getOptionLabel={option => option}
        value={didEdit ? symptomValues : symptoms.map(s => s.medical_term)}
        options={symptomList}
        renderInput={params => (
          <TextField
            {...params}
            label={label}
            sx={{
              fontFamily: "Poppins",
              color: "#36454f",
              marginY: "5px",
              fontSize: "12px"
            }}
            value={didEdit ? symptomValues : symptoms.map(s => s.medical_term)}
          />
        )}
        multiple
        onChange={(e, newValue) => {
          setDidEdit(true);
          setSymptomValues(newValue);
          const sxObjects = newValue.map(medicalTerm => {
            return jsonSymptoms.find(s => s.medical_term === medicalTerm);
          });
          console.log(sxObjects);
          if (id === "symptoms") {
            updateFields({ symptoms: sxObjects });
          } else if (id === "associated-symptoms") {
            updateFields({ associated_symptoms: sxObjects });
          } else {
            updateFields({ minor: sxObjects });
          }
        }}
        freeSolo
      />
    </FormControl>
  );
};
export default SymptomSelector;
